<template>
    <div class="smart-store-home">
        <div class="smart-store-menu">
            <div class="menu-ul">
                <div class="menu-item" v-for='(menu,index) in menus' :id="'smartStoreTab'+index" @click="menuItemClick(index)">
                    <div class="menu-text">{{menu.titleName}}</div>
                </div>
            </div>
        </div>
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
    </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "smart-store-home",
  data() {
    return {
      menus: [
        { path: "smartProjectList", titleName: "项目引进" },
        { path: "coBrands", titleName: "合作品牌" }
      ],
      curIndex: -1
    };
  },
  components: {},
  methods: {
    menuItemClick(index, direct = false) {
      if (!direct && this.curIndex == index) return;
      if (this.curIndex != -1) {
        let lastTab = document.getElementById("smartStoreTab" + this.curIndex);
        lastTab.classList.remove("active");
      }
      let curTab = document.getElementById("smartStoreTab" + index);
      curTab.classList.add("active");
      this.curIndex = index;
      let menu = this.menus[index];
      this.$router.replace(menu.path);
    }
  },
  activated() {
    if (this.curIndex == -1) this.menuItemClick(0);
    else this.menuItemClick(this.curIndex, true);
  },
  mounted() {},
  computed: {
    ...mapState(["path", "icon"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.smart-store-home {
    font-family: 'Microsoft YaHei';
    width: 100%;
    min-height:100%;
    display: flex;
    // padding-top: 0.31rem;
    flex-direction: column;
    // box-sizing: border-box;

    // 导航样式
    .smart-store-menu {
        width 100%;
        flex: 0 1 0.31rem;

        .menu-ul {
            display: flex;
            justify-content: center;

            .menu-item {
                flex: 1;
                text-align: center;
                background-color: #dcdddd;
                height: 0.31rem;

                &.active {
                    background-color: #f8ef94;

                    .menu-text {
                        color: #595656;
                    }
                }

                .menu-text {
                    letter-spacing: 0.5px;
                    font-size: 0.13rem;
                    color: #898989;
                    line-height: 0.31rem;
                }
            }
        }
    }
}
</style>



